:root {
	--add: #85a5b5;
	--sub: #b5a585;
	--mul: #b585a5;
	--div: #543896;
	--modifier: #a03030;
	--result: #62faae;
}

.segment {
	stroke: black;
	stroke-width: 1;
	cursor: pointer;
}

.segment-group-odd .bed-single1,
.segment-group-odd .bed-single2 {
	fill: #171717;
}

.segment-group-even .bed-single1,
.segment-group-even .bed-single2 {
	fill: #bcbcbc;
}

.segment-group-odd .bed-double {
	fill: #5d5d5d;
}

.segment-group-even .bed-double {
	fill: #434343;
}

.segment-group-odd .bed-treble {
	fill: #5d5d5d;
}

.segment-group-even .bed-treble {
	fill: #434343;
}

.segment-group-odd .bed-modifier {
	fill: #5d5d5d;
}

.segment-group-even .bed-modifier {
	fill: #434343;
}

.segment.add {
	fill: var(--add);
}

.segment.sub {
	fill: var(--sub);
}

.segment.mul {
	fill: var(--mul);
}

.segment.div {
	fill: var(--div);
}

.segment.no-operation {
	fill: #bcbcbc;
}

.bed-bull {
	fill: #bcbcbc;
}

.bed-score {
	fill: #303030;
	stroke: none;
}

.outer-ring-radial {
	stroke: #cccccc;
	stroke-width: 4;
}

.numbers-ring-border {
	fill: none;
	stroke: black;
	stroke-width: 4;
}

.score-text {
	fill: #909090;
	font-size: 28px;
	font-weight: bold;
	font-family: 'Cinzel', Arial, sans-serif;
	paint-order: stroke;
	stroke: #505050;
	stroke-width: 2px;
	cursor: pointer;
}

.border-ring {
	fill: none;
	stroke: black;
	stroke-width: 4;
}

g .modifier {
	fill: var(--modifier);
}

g path.modifier {
	stroke: var(--modifier);
}

.result {
	fill: var(--result);
	fill-opacity: 0.4;
}
