:root {
	--add: #85a5b5;
	--sub: #b5a585;
	--mul: #b585a5;
	--div: #543896;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	padding: 0;
	margin: 0;
	line-height: 1.5em;
}

/* potentially duplicated */
ol {
	list-style: none;
	margin: 0;
	padding: 0;
}

li {
	line-height: 1.5em;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html,
body {
	height: 100%;
	overflow: hidden; /* Prevent scrollbar */
	font-family: 'Cinzel', Arial, sans-serif;
}

.container {
	background-color: #202028;
	display: flex;
	height: 100vh; /* Full viewport height */
	width: 100vw; /* Full viewport width */
	overflow: hidden;
	padding: 8px;
}

.left-side {
	display: flex;
	flex-direction: column;
	flex: 2; /* Takes available space, splits with right-side */
	min-width: 0; /* Prevents overflow */
}

.dartboard-container {
	user-select: none;
	position: relative;
	flex: 1; /* Grows to fill available space, minus footer height */
	width: 100%;
	overflow: hidden;
}

.dartboard,
.overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: contain; /* Ensures SVGs scale properly */
}

.overlay {
	pointer-events: none;
}

.footer-component {
	width: 100%;
	/* Fixed height, adjust as needed */
	/* height: 100px; */
	color: #f0f0f0; /* Example styling */
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 1rem 0 3rem;
}

.right-side {
	flex: 1; /* Takes available space, splits with left-side */
	display: flex;
	flex-direction: column;
	padding: 20px;
	overflow: hidden;
}

.right-side ul {
	list-style: none;
	width: 100%;
}

.right-side li {
	padding: 10px 0;
}

button {
	outline: none;
	border-radius: 8px;
	border: 1px solid transparent;
	padding: 0.6em 1.2em;
	font-size: 1em;
	font-weight: 500;
	font-family: inherit;
	transition: border-color 0.25s;
	box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
	color: #ffffff;
	background-color: #0f0f0f98;
	user-select: none;
	cursor: pointer;
}

button:active {
	background-color: #0f0f0f69;
	border-color: #396cd8;
}

button:hover {
	border-color: #396cd8;
}

button:nth-child(1) {
	position: absolute;
	left: 16px;
	top: 16px;
}

button:nth-child(2) {
	position: absolute;
	right: 16px;
	top: 16px;
}

@media (max-width: 767px) {
	.right-side {
		display: none
	}

	.dartboard-container {
		margin: 0;
	}
}
